.card-gallery__ctn{
  max-width: 1000px;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #cdcdcd;
  background-color: #ffffff;
}

.card-gallery__top{
  display: flex;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
  padding:  20px 30px;
  border-bottom: 1px solid #cdcdcd;
}

.card-gallery__links-ctn > ul{
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}
.card-gallery__links-ctn > ul > li{
  position: relative;
}
.card-gallery__links-ctn > ul > li>  button{
  color: #1b4a8b;
  font-weight: 500;
  font-size: 16px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.card-gallery__links-ctn > ul > li:nth-child(n + 2)::before{
  position: absolute;
  content: '>';
  left: -15px;

}

.card-gallery__search-ctn {
  position: relative;
  display: flex;
  align-items: center;
}

.card-gallery__search-ctn > input{
  border: none;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  transition: border 0.3s ease-in-out;
}

.card-gallery__search-ctn > input:focus-visible{
  outline: none;
  border-bottom: 1px solid #1b4a8b;
}
.card-gallery__search-ctn:focus-within > svg{
  fill: #1b4a8b;
}

.card-gallery__search-ctn > svg{
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.card-gallery__cards-ctn{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
  padding: 20px;
}

.card-gallery__cards-ctn > button {
  border: none;
  cursor: pointer;
}

.gallery__empaty {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 20px;
  gap: 20px;
}

.gallery__empaty > p {
  text-align: center;
  max-width: 400px;
  line-height: 1.5rem;
}

.gallery__empaty-email{
  margin-left: 1ch;
  color: #1b4a8b;
  font-weight: 500;
}

@media only screen and (max-width:1000px) {
  .card-gallery__ctn{
    border-radius: 0;
  } 
}